.order-form .form-footer {
  margin-top: 2rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.order-form .files-wrap {
  display: flex;
}
.order-form .file-block {
  margin-right: 40px;
}
.order-form .submit {
  padding: 15px 75px;
  font-size: 14px;
  text-transform: uppercase;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-button {
  padding: 0;
  border: none;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-button:focus {
  box-shadow: none;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-button i {
  color: #3B7A17;
  margin-left: 5px;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .meta {
  margin-right: 0;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .meta .upload-remove-button {
  top: 0;
  right: 6px;
  line-height: 1;
  padding: 0;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .info h4 {
  max-width: 200px;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .info p.size {
  display: none !important;
}

@media (max-width: 1199px) {
  .order-form .form-control {
    max-width: 220px;
  }
  .order-form .file-block {
    margin-right: 20px;
    width: 50%;
  }
  .order-form .submit {
    padding: 15px 45px;
  }
  .order-form .responsiv-uploader-fileupload.style-file-multi .upload-button {
    text-align: left;
    font-size: 14px;
  }
}
@media (max-width: 991px) {
  .order-form .form-footer,
.order-form .files-wrap {
    flex-direction: column;
    align-items: flex-start;
  }
  .order-form .form-footer {
    margin-top: 30px;
  }
  .order-form .submit {
    width: 100%;
  }
  .order-form .file-block {
    margin-right: 0;
    margin-bottom: 20px;
    width: 100%;
  }
  .order-form .file-btn {
    text-align: left;
  }
  .order-form .form-control {
    max-width: 100%;
    flex: 1 1;
  }
  .order-form .form-label {
    min-width: 190px;
  }
}
.modal {
  background: rgba(247, 243, 232, 0.82);
}
.modal .modal-content {
  border: none;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
}
.modal .close-modal {
  position: absolute;
  top: 0;
  right: 0;
}
.modal .close-modal:focus {
  box-shadow: none;
}
.modal .modal-title {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 50px;
}

.success-modal {
  font-size: 24px;
  text-align: center;
}
.success-modal .modal-dialog {
  max-width: 715px;
}
.success-modal .success-wrap {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 552px;
}
.success-modal .success-message-logo {
  width: 170px;
  margin-bottom: 40px;
}
.success-modal .success-message-title {
  text-transform: uppercase;
  margin-bottom: 30px;
}
.success-modal .success-message-order {
  margin-bottom: 40px;
}

@media (max-width: 767px) {
  .success-modal {
    font-size: 18px;
  }
  .success-modal .modal-content {
    min-height: 350px;
  }
}
.cart-page .order-form .form-footer {
  align-items: flex-end;
}
.cart-page .add-to-order:hover {
  color: #fff;
}
.cart-page .cart-empty {
  padding-bottom: 30px;
  border-bottom: 1px solid #DCC6A4;
}
.cart-page .cart-item {
  position: relative;
  display: flex;
  padding-bottom: 30px;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 14px;
  border-bottom: 1px solid #DCC6A4;
}
.cart-page .cart-item:last-child {
  margin-bottom: 0;
}
.cart-page .cart-item picture {
  margin-right: 50px;
}
.cart-page .cart-info-wrap {
  min-width: 760px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cart-page .cart-name {
  font-size: 18px;
  font-weight: 700;
}
.cart-page .cart-info {
  display: flex;
  justify-content: space-between;
}
.cart-page .cart-settings {
  color: #707070;
}
.cart-page .cart-settings div {
  margin-right: 30px;
}
.cart-page .cart-settings div span:first-child {
  margin-right: 5px;
}
.cart-page .cart-box-count {
  margin-left: 8px;
}
.cart-page .cart-box-price {
  padding: 2px 8px;
  background-color: #F7F3E8;
}
.cart-page .remove-cart {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  position: absolute;
  right: 0;
  top: 0;
  border: none;
  background-color: transparent;
  padding: 0;
}
.cart-page .item-icon {
  padding: 3px;
  border: 1px solid #dfbd97;
  border-radius: 5px;
}
.cart-page .total-price-wrap {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  padding: 30px 0;
}
.cart-page .total-price-wrap .total-price-block {
  display: flex;
  align-items: center;
}
.cart-page .total-price-wrap .total-price {
  margin-left: 5px;
  font-size: 24px;
  font-weight: 500;
}
.cart-page .user-data {
  padding: 40px;
  background-color: #FCFCFC;
  border-radius: 0px 0px 7px 7px;
  margin-bottom: 60px;
}
.cart-page .files-wrap {
  flex-direction: column;
}
.cart-page .file-block {
  position: relative;
  margin-right: 0;
}
.cart-page .file-block .upload-button {
  padding: 10px 0 !important;
  font-size: 18px;
  margin-bottom: 0 !important;
}
.cart-page .file-block .btn-popover {
  padding: 0;
  position: absolute;
  right: 40px;
  top: 10px;
}
.cart-page .file-block .btn-popover:focus {
  box-shadow: none;
}

@media (max-width: 1199px) {
  .cart-page .cart-info-wrap {
    min-width: auto;
    width: 100%;
  }
  .cart-page .file-block {
    width: 100%;
  }
  .cart-page .file-block .btn-popover {
    right: 30px;
    top: 8px;
  }
}
@media (max-width: 991px) {
  .cart-page .order-form .form-footer {
    align-items: flex-start;
  }
  .cart-page .cart-name {
    margin-bottom: 15px;
  }
  .cart-page .cart-info {
    flex-direction: column;
  }
  .cart-page .cart-settings {
    margin-bottom: 10px;
  }
  .cart-page .cart-price {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .cart-page .cart-box-count {
    margin-right: 20px;
  }
  .cart-page .files-wrap {
    margin-bottom: 40px;
  }
  .cart-page .file-block {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .cart-page .user-data {
    padding: 20px;
  }
  .cart-page .cart-item picture {
    margin-right: 30px;
  }
  .cart-page .cart-item picture img {
    width: 64px;
  }
  .cart-page .cart-settings div:first-child {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 575px) {
  .cart-page .add-to-order {
    text-align: center;
  }
  .cart-page .cart-name {
    font-size: 14px;
    max-width: 85%;
  }
  .cart-page .cart-settings div:first-child {
    margin-right: 0;
  }
  .cart-page .cart-settings div span:first-child {
    margin-right: 0;
  }
  .cart-page .user-data {
    margin-bottom: 0;
  }
  .cart-page .cart-item picture {
    margin-right: 20px;
  }
  .cart-page .cart-item picture img {
    width: 44px;
  }
  .cart-page .order-form {
    padding: 40px 0;
  }
  .cart-page .total-price-wrap {
    flex-direction: column-reverse;
    font-size: 14px;
    justify-content: center;
  }
  .cart-page .total-price-wrap .total-price-block {
    margin-bottom: 15px;
  }
  .cart-page .total-price-wrap .total-price {
    font-size: 20px;
  }
  .cart-page .file-block .btn-popover {
    right: 0;
  }
}